<template>
  <div class="content">
    <div class="summary" v-if="!isMobile">
      <img src="../../assets/images/ying.jpg">
      莹好可爱
      <p>{{totalDay}}days</p>
    </div>
    <div class="main">
      <v-touch
        tag="div"
        :enabled="isMobile"
        @swipeleft="nextMonth"
        @swiperight="prevMonth"
        :swipe-options="{direction: 'horizontal'}"
      >
        <div class="calendar" id="calendar">
          <div :class="['cal-head',{'is-date': selectedDate.isDate},{'is-memorable': selectedDate.isMemorable}]">
            <i v-show="!isMobile && isPrevMonthEnabled" class="iconfont icon-prev" @click="prevMonth" :title="'上月：'+prevMonthStr"></i>
            <i v-show="!isMobile && isNextMonthEnabled" class="iconfont icon-next" @click="nextMonth" :title="'下月：'+nextMonthStr"></i>
            <div class="cal-border"></div>
            <span>日</span>
            <span>一</span>
            <span>二</span>
            <span>三</span>
            <span>四</span>
            <span>五</span>
            <span>六</span>
          </div>
          <div class="cal-body">
            <div v-for="(item, index) in dateList" :key="index"
              :class="['date-item',{'not-current': item.month!=='CURRENT_MONTH'},{'is-special': item.detail.content},{'is-date': item.detail.isDate},{'is-memorable': item.detail.isMemorable},{'is-chosen': item.detail.date===selectedDate.date}]"
            >
              <div class="date-content" @click="selectDate(item)">
                <span class="date-num">{{ item.date }}</span>
                <div v-if="item.detail.isDate || item.detail.content"
                  :class="['memory-wrap', {'memory-wrap-right':(index%7)>4}]"
                >
                  <i class="arrow"></i>
                  <div class="memory-content">
                    <div class="memory-info">
                      <p><i class="iconfont icon-calendar"></i>{{item.timestamp | formatTime}}</p>
                      <p v-if="item.detail.location"><i class="iconfont icon-location"></i>{{item.detail.location}}</p>
                    </div>
                    <div class="memory-detail" v-html="item.detail.content"></div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </v-touch>

      <!-- 只在分辨率<=768时显示 -->
      <div v-if="isMobile" :class="['memory-wrap', 'memory-wrap-sm',{'is-date': selectedDate.isDate},{'is-memorable': selectedDate.isMemorable}]">
        <div class="memory-content">
          <!-- <div class="memery-border"></div> -->
          <div class="memory-info">
            <p><i class="iconfont icon-calendar"></i>{{selectedDate.date}}</p>
            <p v-if="selectedDate.location"><i class="iconfont icon-location"></i>{{selectedDate.location}}</p>
          </div>
          <!-- <div class="memory-detail" v-html="selectedDate.content"></div> -->
          <div class="memory-detail" id="type-memory-detail"></div>
        </div>
      </div>

      <div class="wechat" v-if="selectedDate.wechat">
        <p class="wechat-date">{{selectedDate.date}}</p>
        <div v-for="(item, index) in selectedDate.wechat" :key="index"
          :class="['wechat-item', {'wechat-item-mine': item.isMine}]"
        >
          <img class="wechat-avatar" src="../../assets/images/avatar-ying.jpeg">
          <img class="wechat-avatar wechat-avatar-mine" src="../../assets/images/avatar-yin.jpeg">
          <p class="wechat-content">
            <i class="arrow"></i>
            <span>{{item.content}}</span>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import loveStories from '@/assets/data/loveStories'
import { init } from 'ityped'

export default {
  name: 'loveCalendar',
  data () {
    return {
      startDate: '2019/2/3',
      year: 2019,
      month: 2,
      dateList: [],
      selectedDate: {},
      minYear: 2019,
      minMonth: 2,
      maxYear: new Date().getFullYear(),
      maxMonth: new Date().getMonth() + 1,
      typeElement: null,
      isTyping: false
    }
  },
  mounted () {
    window.document.body.style.backgroundColor = '#fff'
    document.title = `${this.totalDay} days since ${this.startDate}`
    this.getDateArray()
    this.typeElement = this.isMobile ? document.querySelector('#type-memory-detail') : null
  },
  methods: {
    // 获取日期数组
    getDateArray (year = this.year, month = this.month) {
      const currentMonth = new Date([year, month, '1'].join('/'))
      const nextMonth = month === 12 ? new Date([year + 1, 1, '1'].join('/')) : new Date([year, month + 1, '1'].join('/'))
      const oneDayMills = 24 * 60 * 60 * 1000 // 毫秒
      let firstDay = currentMonth.getDay() // 第一天星期几
      let beforeFix = []

      for (let i = 0; i < firstDay; i++) {
        let date = new Date(currentMonth - oneDayMills * (i + 1))
        const timestamp = date.getTime()
        const detail = this.getDateInfo(timestamp)
        const obj = {
          month: 'LAST_MONTH',
          date: date.getDate(),
          timestamp,
          detail
        }
        beforeFix.unshift(obj)
      }

      let currentDate = []
      let lastDate = new Date(nextMonth - oneDayMills).getDate() // 最后一天日期

      for (let i = 0; i < lastDate; i++) {
        const timestamp = currentMonth.getTime() + oneDayMills * i
        const date = new Date(timestamp).getDate()
        const detail = this.getDateInfo(timestamp)
        const obj = {
          month: 'CURRENT_MONTH',
          date: date,
          timestamp,
          detail
        }
        currentDate.push(obj)
        // 选中当月第一天
        if (!this.selectedDate.date && detail.date) {
          this.selectedDate = detail
        }
      }

      let lastDay = nextMonth.getDay()
      let afterFix = []
      if (lastDay !== 0) {
        for (let i = 0; i < 7 - lastDay; i++) {
          const timestamp = nextMonth.getTime() + oneDayMills * i
          const date = new Date(timestamp).getDate()
          const detail = this.getDateInfo(timestamp)
          const obj = {
            month: 'NEXT_MONTH',
            date: date,
            timestamp,
            detail
          }
          afterFix.push(obj)
        }
      }

      this.dateList = beforeFix.concat(currentDate).concat(afterFix)
    },
    // 获取当天数据
    getDateInfo (timestamp) {
      const date = new Date(timestamp)
      const dateStr = `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDate()}`
      const index = loveStories.findIndex(item => {
        return item.date === dateStr
      })
      return index === -1 ? {} : loveStories[index]
    },
    // 选择日期
    selectDate (dateItem) {
      if (!dateItem.detail.content) return

      if (this.checkIsTyping()) {
        return
      }

      if (dateItem.month === 'NEXT_MONTH') {
        this.nextMonth(dateItem.detail)
        return
      }
      if (dateItem.month === 'LAST_MONTH') {
        this.prevMonth(dateItem.detail)
        return
      }

      this.selectedDate = dateItem.detail
    },
    // 日历下月
    nextMonth (selectedDate = {}) {
      if (this.checkIsTyping()) {
        return
      }

      if (!this.isNextMonthEnabled) {
        this.$toast('以后我们慢慢过日子~')
        return
      }

      if (this.month === 12) {
        this.month = 1
        this.year += 1
      } else {
        this.month += 1
      }
      this.selectedDate = selectedDate
      this.getDateArray()
    },
    // 日历上月
    prevMonth (selectedDate = {}) {
      if (this.checkIsTyping()) {
        return
      }

      if (!this.isPrevMonthEnabled) {
        this.$toast('以前我们还不认识~')
        return
      }

      if (this.month === 1) {
        this.month = 12
        this.year -= 1
      } else {
        this.month -= 1
      }
      this.selectedDate = selectedDate
      this.getDateArray()
    },
    checkIsTyping () {
      if (!this.isTyping) return false

      this.$toast('流水账还没报完呢~')
      return true
    }
  },
  computed: {
    isMobile () {
      return !!navigator.userAgent.match(/(iPhone|iPod|Android|ios|SymbianOS)/i)
    },
    isPrevMonthEnabled () {
      return this.month !== this.minMonth || this.year !== this.minYear
    },
    isNextMonthEnabled () {
      return this.month !== this.maxMonth || this.year !== this.maxYear
    },
    totalDay () {
      const today = new Date().getTime()
      const startDay = new Date(this.startDate).getTime()

      return Math.floor((today - startDay) / (24 * 60 * 60 * 1000))
    },
    nextMonthStr () {
      let year = this.year
      let month = this.month
      if (month === 12) {
        month = 1
        year += 1
      } else {
        month += 1
      }
      return `${year}/${month}`
    },
    prevMonthStr () {
      let year = this.year
      let month = this.month
      if (month === 1) {
        month = 12
        year -= 1
      } else {
        month -= 1
      }
      return `${year}/${month}`
    }
  },
  destroyed () {
    window.document.body.style.backgroundColor = this.bgColor
  },
  watch: {
    selectedDate (date) {
      if (this.isMobile && date.content) {
        this.typeElement.innerHTML = ''
        this.isTyping = true
        init(this.typeElement, {
          typeSpeed: 150,
          startDelay: 0,
          showCursor: false,
          strings: [date.content],
          loop: false,
          onFinished: () => {
            this.isTyping = false
          }
        })
      }
    }
  }
}
</script>

<style lang="scss" scoped>
